@font-face {
  font-family: "icomoon";
  src: url(/weather-forecast/static/media/icomoon.f4ae08df.eot);
  src: url(/weather-forecast/static/media/icomoon.f4ae08df.eot) format("embedded-opentype"),
    url(/weather-forecast/static/media/icomoon.9e94e87c.ttf) format("truetype"),
    url(/weather-forecast/static/media/icomoon.fcf32c41.woff) format("woff"),
    url(/weather-forecast/static/media/icomoon.36ba6e52.svg) format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

.icon-sun:before {
  content: "\E902";
}
.icon-moon:before {
  content: "\E903";
}
.icon-wind:before {
  content: "\E901";
}
.icon-cloudy:before {
  content: "\E904";
}
.icon-cloud:before {
  content: "\E905";
}
.icon-weather1:before {
  content: "\E900";
}
.icon-rainy:before {
  content: "\E906";
}
.icon-snowy:before {
  content: "\E907";
}
.icon-weather:before {
  content: "\E908";
}
.icon-cloudy1:before {
  content: "\E909";
}

.background-image::after {
  content: "";
  opacity: 0.5;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;
  background-image: url(/weather-forecast/static/media/bk.cb93c15e.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

/* Hide scroll bar, but while still being able to scroll */
.carousel-wrap {
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE 10+ */
}
.carousel-wrap::-webkit-scrollbar {
  width: 0;
  height: 0;
}

/* Workaround for flex bug :(
 * minimum content sizing of flex items not honored 
 * https://github.com/philipwalton/flexbugs#flexbug-1 */
.flex-minimum-content-sizing {
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  -webkit-flex-basis: auto;
          flex-basis: auto;
}

